.triangle {
    position: relative;
    height:30%;
    width:35%;
    -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
            clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
} 

.square {
    height: 25%;
    width: 25%;
    border-width: 2px;

}

.circle {
    height: 25%;
    width: 25%;
    border-radius: 50%;
    border-width: 2px;
}
.card-container {
    width: 160px;
    height: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card {
    width: 140px;
    height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border-style: solid; */
    border-radius: 5px;
    border-width: 2px;

    transition-property: width, height;
    transition-duration: .1s;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

.card:hover {
    width:150px;
    height: 150px;;
}

.square , .circle {
    /* border-style: solid; */
}


.game-board {
    height: 650px;
    width: 650px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
